.about {
    width: 100%;
    height: 100vh;
}

.wraper {
    width: 100%;
    height: 100%;
    background-color: #00000099;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.backgroundImage1 {
    background: url('../images/oborudovanie.jpeg') no-repeat;
    background-size: cover;
    background-position: center;
    position: absolute;
    width: 100%;
    height: 100vh;
    z-index: -1;
}

.backgroundImage3 {
    background: url('../images/bg-mic.jpeg') no-repeat;
    background-size: cover;
    background-position: center;
    position: absolute;
    width: 100%;
    height: 100vh;
    z-index: -1;
}

.backgroundImage4 {
    background: url('../images/bg-money.jpeg') no-repeat;
    background-size: cover;
    background-position: center;
    position: absolute;
    width: 100%;
    height: 100vh;
    z-index: -1;
}

.container {
    width: 1440px;
    height: 100vh;
    display: flex;
    align-items: center;
}

.aboutText {
    width: 50%;
    color: #ffffff;
}

.aboutHeading {
    font-size: 45px;
    margin-bottom: 30px;
}

.aboutP {
    font-size: 22px;
}

.aboutBtns {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50%;
    height: 500px;
}

.btnsContainer {
    display: flex;
    justify-content: center;
    width: 100%;
}

.aboutBtn {
    width: 150px;
    height: 150px;
    background: #ffffff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.money {
    margin-left: 40px;
}

.active {
    background-color: #28926C;
}

.btnImg {
    width: 70px;
    height: 70px;
    pointer-events: none;
}

@media screen and (max-width: 1500px) {
    .container {
        width: 1200px
    }
    
    .aboutHeading {
        font-size: 35px;
    }
    
    .aboutP {
        font-size: 20px;
    }
}

@media screen and (max-width: 1250px) {
    .container {
        width: 1000px
    }
    
    .aboutHeading {
        font-size: 30px;
    }
    
    .aboutP {
        font-size: 18px;
    }
    
    .aboutBtn {
        width: 120px;
        height: 120px;
    }
    
    .pc {
        right: 330px;
    }
    
    .mic {
        right: 70px;
    }
    
    .money {
        bottom: 100px;
    }
    
