.main {
    width: 100%;
    height: 100vh;
    background: url('../images/скорая.jpeg') no-repeat;
    background-size: cover;
}

.wraper {
    width: 100%;
    height: 100%;
    background-color: #00000099;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    width: 1440px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mainText {
    width: 50%;
    color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.mainTextHeading {
    font-weight: 600;
    font-size: 50px;
    margin-bottom: 50px;
}

.mainTextP {
    font-size: 23px;
    font-weight: 400;
}

.mainBlocks {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.mainBlock {
    width: 400px;
    height: 134px;
    display: flex;
    align-items: center;
    background-color: #28926C;
    border-radius: 32px;
}

.mainBlock:nth-child(n + 2) {
    margin-top: 20px;
}

.blockImg {
    width: 70px;
    height: 70px;
    margin-left: 20px;
}

.blockText {
    margin-left: 20px;
    margin-right: 20px;
    color: #ffffff;
    font-weight: 700;
    font-size: 16px;
}

@media screen and (max-width: 1500px) {
    .container {
        width: 1200px;
    }

    .mainBlock {
        width: 300px;
        height: 100px;
    }

    .blockImg {
        width: 50px;
        height: 50px;
    }

    .mainTextHeading {
        font-size: 40px;
        margin-bottom: 30px;
    }

    .mainTextP {
        font-size: 18px;
    }
}

@media screen and (max-width: 1250px) {
    .container {
        width: 1000px;
    }

    .mainBlock {
        width: 300px;
        height: 100px;
    }

    .blockImg {
        width: 50px;
        height: 50px;
    }

    .mainTextHeading {
        font-size: 40px;
        margin-bottom: 30px;
    }

    .mainTextP {
        font-size: 18px;
    }
}

@media screen and (max-width: 1050px) {
    .container {
        width: 800px;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
    }

    .mainBlocks {
        width: 800px;
        flex-wrap: wrap;
        flex-direction: row;
        margin-top: 30px;
    }

    .mainBlock:nth-child(2n + 2) {
        margin-left: 30px;
    }

    .mainBlock:nth-child(n + 3) {
        margin-top: 20px;
    }

    .blockImg {
        width: 50px;
        height: 50px;
    }

    .mainText {
        width: 100%;
    }

    .mainTextHeading {
        font-size: 40px;
        margin-bottom: 30px;
    }

    .mainTextP {
        width: 100%;
        font-size: 18px;
    }
}

@media screen and (max-width: 850px) {
    .container {
        width: 600px;
    }

    .mainBlocks {
        width: 600px;
    }

    .mainBlock {
        width: 250px;
        height: 80px;
    }

    .blockText {
        font-size: 14px;
    }

    .blockImg {
        width: 40px;
        height: 40px;
    }

    .mainTextHeading {
        font-size: 30px;
        margin-bottom: 30px;
    }

    .mainTextP {
        font-size: 16px;
    }
}

@media screen and (max-width: 650px) {
    .container {
        width: 400px;
        height: 100vh;
        justify-content: center;
    }

    .mainBlocks {
        width: 400px;
        align-items: center;
        justify-content: center;
    }

    .mainBlock {
        width: 400px;
        height: 70px;
        border-radius: 20px;
    }

    .blockText {
        font-size: 12px;
    }

    .mainBlock:nth-child(2n + 2) {
        margin-left: 0;
    }

    .mainBlock:nth-child(n + 2) {
        margin-top: 20px;
    }

    .blockImg {
        width: 30px;
        height: 30px;
    }

    .mainTextHeading {
        font-size: 25px;
        margin-bottom: 20px;
    }

    .mainTextP {
    }
}

@media screen and (max-width: 425px) {
    .container {
        width: 300px;
        height: 100vh;
        justify-content: center;
    }

    .mainBlocks {
        width: 300px;
        align-items: center;
        justify-content: center;
    }

    .mainBlock {
        width: 300px;
        height: 60px;
        border-radius: 20px;
    }

    .blockText {
        font-size: 12px;
    }

    .mainBlock:nth-child(2n + 2) {
        margin-left: 0;
    }

    .mainBlock:nth-child(n + 2) {
        margin-top: 20px;
    }

    .blockImg {
        width: 30px;
        height: 30px;
    }

    .mainTextHeading {
        font-size: 25px;
        margin-bottom: 20px;
    }

    .mainTextP {
        width: 300px;
        font-size: 14px;
    }
}