.price {
    width: 100%;
    height: auto;
    padding: 50px 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    width: 1440px;
}

.priceHeading {
    font-weight: 600;
    font-size: 36px;
    margin-bottom: 50px;
}

.priceItems {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.priceItem {
    width: 100%;
    height: 100px;
    background-color: #F5F8F7;
    border-radius: 32px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.priceItem:nth-child(n + 2) {
    margin-top: 50px;
}

.itemHeading {
    font-weight: 500;
    font-size: 22px;
    margin-left: 50px;
}

.itemPrice {
    color: #28926C;
    font-weight: 600;
    font-size: 22px;
    margin-right: 50px;
}

@media screen and (max-width: 1500px) {
    .container {
        width: 1200px;
    }
    
    .priceHeading {
        font-size: 30px;
    }
}

@media screen and (max-width: 1250px) {
    .container {
        width: 1000px;
    }
    
    .priceItem:nth-child(n + 2) {
        margin-top: 30px;
    }
    
    .itemHeading {
        font-size: 20px;
    }
    
    .itemPrice {
        font-size: 20px;
    }
}

@media screen and (max-width: 1050px) {
    .container {
        width: 800px;
    }
    
    .priceHeading {
        font-size: 25px;
    }
    
    .priceItem {
        height: 80px;
        border-radius: 25px;
    }
    
    .itemHeading {
        font-size: 18px;
    }
    
    .itemPrice {
        font-size: 18px;
    }
}

@media screen and (max-width: 850px) {
    .container {
        width: 600px;
    }
    
    .priceHeading {
        font-size: 22px;
    }
    
    .itemHeading {
        font-size: 16px;
    }
    
    .itemPrice {
        font-size: 16px;
    }
}

@media screen and (max-width: 650px) {
    .container {
        width: 400px;
    }
    
    .priceHeading {
        font-size: 20px;
        margin-bottom: 30px;
    }
    
    .priceItem {
        height: 70px;
        border-radius: 20px;
    }
    
    .itemHeading {
        font-size: 14px;  
        margin-left: 30px; 
    }
    
    .itemPrice {
        font-size: 14px;
        margin-right: 20px;
    }
}

@media screen and (max-width: 425px) {
    .container {
        width: 300px;
    }
    
    .priceHeading {
        font-size: 16px;
    }
    
    .itemHeading {
        font-size: 10px;
        margin-left: 10px;
    }
    
    .itemPrice {
        font-size: 10px;
        margin-right: 10px;
    }
}