* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-tap-highlight-color: transparent;
}

body {
    width: 100%;
    font-family: 'Montserrat', sans-serif;
}

#root {
    width: 100%;
}

.App {
    width: 100%;
    position: relative;
}

::-webkit-scrollbar {
    display: none;
}