.popup {
    position: fixed;
    right: 0;
    top: 70px;
    width: 300px;
    height: 100vh;
    background-color: #ffffffcc;
    display: flex;
    z-index: 2;
}

.navBar {
    width: 100%;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.navBarItem {
    align-self: flex-start;
    margin-left: 30px;
    list-style: none;
    font-weight: 600;
    font-size: 18px;
    cursor: pointer;
    color: #000000;
    text-decoration: none;
}

.navBarItem:nth-child(n + 2) {
    margin-top: 20px;
}

.navBarItem:hover {
    text-decoration: underline;
}

@media screen and (max-width: 425px) {
    .popup {
        width: 200px;
        top: 50px;
    }
}