.contact {
    width: 100%;
    height: auto;
    padding: 50px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #F5F8F7;
}

.wraper {
    width: 1440px;
}

.contactHeading {
    font-weight: 600;
    font-size: 36px;
    margin-bottom: 50px;
}

.container {
    width: 100%;
    height: 700px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 32px;
}

.containerImg {
    width: 50%;
    height: 100%;
    border: none;
    border-top-left-radius: 32px;
    border-bottom-left-radius: 32px;
}

.containerText {
    margin-right: 100px;
}

.containerHeading {
    width: 100%;
    font-weight: 600;
    font-size: 28px;
    margin-top: 50px;
    margin-bottom: 50px;
}

.containerAddress {
    font-weight: 400;
    font-size: 18px;
    line-height: 40px;
    margin-left: 50px;
    position: relative;
}

.containerAddress::before {
    position: absolute;
    display: block;
    content: url('../images/gps.svg');
    width: 50px;
    height: 50px;
    left: -60px;
    top: 15px;
}

@media screen and (max-width: 1500px) {
    .wraper {
        width: 1200px;
    }
    
    .contactHeading {
        font-size: 30px;
    }
    
    .containerText {
        margin-right: 0;
    }
    
    .containerHeading {
        font-size: 25px;
        padding-right: 20px;
    }
}

@media screen and (max-width: 1250px) {
    .wraper {
        width: 1000px;
    }

    .container {
        height: 500px;
    }
    
    .containerText {
        width: 50%;
        padding-left: 30px;
    }
    
    .containerHeading {
        font-size: 22px;
        margin-bottom: 30px;
    }
    
    .containerAddress {
        font-size: 14px;
    }
}

@media screen and (max-width: 1050px) {
    .wraper {
        width: 800px;
    }
    
    .containerHeading {
        font-size: 18px;
    }
}

@media screen and (max-width: 850px) {
    .wraper {
        width: 600px;
    }
    
    .contactHeading {
        font-size: 22px;
    }
    
    .container {
        height: 700px;
        flex-direction: column-reverse;
    }
    
    .containerImg {
        width: 100%;
        height: 50%;
        border-radius: 0;
        border-bottom-left-radius: 32px;
        border-bottom-right-radius: 32px;
    }
    
    .containerText {
        width: 100%;
        height: 50%;
    }
    
    .containerAddress {
        line-height: 30px;
    }
}

@media screen and (max-width: 650px) {
    .wraper {
        width: 400px;
    }
    
    .contactHeading {
        font-size: 20px;
    }
    
    .containerAddress::before {
        width: 40px;
        height: 40px;
        left: -50px;
    }
}

@media screen and (max-width: 425px) {
    .wraper {
        width: 300px;
    }

    .container {
        height: 600px;
    }
    
    .containerHeading {
        font-size: 16px;
        padding-right: 0;
        margin-top: 30px;
    }
    
    .containerAddress {
        font-size: 12px;
        margin-left: 50px;
        line-height: 25px;
    }
}

