.info {
    width: 100%;
    min-height: 100vh;
    padding: 50px 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.wraper {
    width: 1440px;
}

.infoHeading {
    font-weight: 600;
    font-size: 36px;
    margin-bottom: 50px;
}

.content {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
}

.infoItem {
    width: 450px;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    margin-left: 30px;
    padding: 20px 30px 20px 30px;
    background-color: #F5F8F7;
    border-radius: 32px;
    margin-left: 45px;
    cursor: pointer;
    outline: none;
}

.infoItem:nth-child(3n + 1) {
    margin-left: 0px;
}

.infoItem:nth-child(n + 4) {
    margin-top: 50px;
}

@media screen and (max-width: 1500px) {
    .wraper {
        width: 1200px;
    }
    
    .infoHeading {
        font-size: 30px;
    }
    
    .infoItem:nth-child(2n + 1) {
        margin-left: 0;
    }

    .infoItem:nth-child(2n + 2) {
        margin-left: 45px;
    }
    
    .infoItem:nth-child(n + 3) {
        margin-top: 50px;
    }
}

@media screen and (max-width: 1250px) {
    .wraper {
        width: 1000px;
    }
    
    .infoHeading {
        font-size: 28px;
    }

    .infoItem {
       font-size: 16px;
    }
}

@media screen and (max-width: 1050px) {
    .info {
        height: auto;
    }

    .wraper {
        width: 800px;
        margin: 50px 0 50px 0;
    }
    
    .infoHeading {
        text-align: center;
        font-size: 22px;
        margin-bottom: 30px;
    }
    
    .content {
        justify-content: center;
    }
    
    .infoItem {
        font-size: 14px;
        line-height: 18px;
    }
    
    .infoItem:nth-child(3n + 1) {
        margin: 0;
    }
    
    .infoItem:nth-child(n + 4) {
        margin: 0;
    }

    .infoItem:nth-child(n + 3) {
        margin-top: 0;
    }

    .infoItem:nth-child(2n + 2) {
        margin: 0;
    }

    .infoItem:nth-child(n + 2) {
        margin-top: 20px;
    }
}

@media screen and (max-width: 850px) {
    .wraper {
        width: 600px;
    }
    
    .infoHeading {
        font-size: 20px;
    }
    
    .infoItem {
        font-size: 12px;
    }
}

@media screen and (max-width: 650px) {
    .wraper {
        width: 400px;
    }
}

@media screen and (max-width: 425px) {
    .wraper {
        width: 300px;
    }

    .infoHeading {
        font-size: 16px;
    }
}

