.header {
    width: 100%;
    height: 70px;
    background-color: #ffffffcc;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
}

.container {
    width: 1440px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.headerLogo {
    width: 200px;
    height: 60px;
    pointer-events: none;
    cursor: pointer;
}

.navBar {
    display: flex;
}

.navBarItem {
    list-style: none;
    font-weight: 600;
    font-size: 18px;
    cursor: pointer;
    color: #000000;
    text-decoration: none;
}

.navBarItem:hover {
    text-decoration: underline;
}

.navBarItem:nth-child(n + 2) {
    margin-left: 40px;
}

.call {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.callNumber {
    font-weight: bold;
    margin-right: 20px;
}

.navBtn {
    width: 25px;
    height: 25px;
    display: none;
}

.btnImg {
    width: 25px;
    height: 25px;
}

@media screen and (max-width: 1500px) {
    .container {
        width: 1200px;
    }
}

@media screen and (max-width: 1250px) {
    .container {
        width: 1000px;
    }
    
    .navBarItem:nth-child(n + 2) {
        margin-left: 20px;
    }
    
    .callNumber {
        font-size: 20px;
    }
}

@media screen and (max-width: 1050px) {
    .container {
        width: 800px;
    }
    
    .navBar {
        display: none;
    }

    .navBtn {
        display: block;
    }
    
    .navBarItem:nth-child(n + 2) {
        margin-left: 20px;
    }
    
    .callNumber {
        font-size: 20px;
    }
}

@media screen and (max-width: 850px) {
    .container {
        width: 100%;
        padding: 0 50px 0 50px;
    }

    .headerLogo {
        width: 150px;
        height: 40px;
    }
    
    .callNumber {
        font-size: 16px;
    }
}

@media screen and (max-width: 650px) {
    .callNumber {
        display: none;
    }
}

@media screen and (max-width: 425px) {
    .header {
        height: 50px;
    }

    .container {
        width: 100%;
        padding: 0 10px;
    }

    .headerLogo {
        width: 120px;
        height: 35px;
    }

    .callNumber {
        display: none;
    }
}