.call {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #F5F8F7;
    padding: 50px 0;
}

.wrapper {
    width: 1440px;
}

.callHeading {
    font-weight: 600;
    font-size: 36px;
    margin-bottom: 50px;
}

.callStructure {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.arrow {
    width: 200px;
}

.structureBlock {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
}

.blockNum {
    width: 100px;
    height: 100px;
    text-align: center;
    line-height: 100px;
    border-radius: 50%;
    background-color: #28926C;
    color: #ffffff;
    font-weight: 600;
    font-size: 22px;
}

.blockText {
    font-weight: 500;
    font-size: 22px;
    margin-top: 20px;
}

.callWidget {
    width: 100%;
    height: 500px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 32px;
    margin-top: 50px;
}

.widgetContent {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-left: 100px;
}

.contentText {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.textHeading {
    font-weight: 600;
    font-size: 30px;
    margin-bottom: 10px;
}

.textP {
    font-weight: 500;
    font-size: 20px;
}

.contentForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 40px;
}

.formInputs {
    display: flex;
    align-items: center;
    margin-bottom: 50px;
}

.formInput {
    border: none;
    width: 250px;
    height: 40px;
    padding: 5px 0 5px 0;
    font-size: 17px;
    font-weight: 400;
    outline: none;
    background-image: linear-gradient(#000000cc, #000000cc);
    background-position: 0% 100%;
    background-repeat: no-repeat;
    background-size: 100% 2px;
}

.formInput:focus {
    background-image: linear-gradient(#28926C, #28926C);
}

.formInput:focus::placeholder {
    color: #28926C;
}

.formInput:nth-child(n + 2) {
    margin-left: 50px;
}

.formBtn {
    width: 250px;
    height: 60px;
    border-radius: 32px;
    font-size: 18px;
    font-weight: 400;
    cursor: pointer;
    border: none;
    color: #ffffff;
    background-color: #28926C;
}

.widgetImage {
    width: 500px;
    height: 500px;
    background: url('../images/call-photo.webp');
    background-size: cover;
    background-position: center;
    border-top-right-radius: 32px;
    border-bottom-right-radius: 32px;
}

@media screen and (max-width: 1500px) {
    .wrapper {
        width: 1200px;
    }
    
    .callHeading {
        font-size: 30px;
    }
    
    .arrow {
        width: 150px;
    }
    
    .blockText {
       font-size: 20px;
    }
    
    .widgetContent {
        margin-left: 50px;
    }
    
    .textHeading {
        font-size: 25px;
    }
    
    .textP {
        font-size: 18px;
    }
    
    .formInput:nth-child(n + 2) {
        margin-left: 30px;
    }
    
    .widgetImage {
        width: 400px;
    }
}

@media screen and (max-width: 1250px) {
    .wrapper {
        width: 1000px;
    }
    
    .callHeading {
        font-size: 30px;
    }
    
    .arrow {
        width: 120px;
    }
    
    .blockNum {
        width: 80px;
        height: 80px;
        font-size: 20px;
        line-height: 80px;
    }
    
    .blockText {
       font-size: 16px;
    }
}

@media screen and (max-width: 1050px) {
    .wrapper {
        width: 800px;
    }
    
    .callHeading {
        font-size: 25px;
    }

    .arrow {
        width: 100px;
    }
    
    .blockText {
       font-size: 14px;
    }
    
    .callWidget {
        height: 400px;
    }
    
    .widgetContent {
        width: 100%;
        height: 100%;
        margin-left: 0;
        padding-left: 50px;
    }

    .formInput:nth-child(n + 2) {
        margin-left: 50px;
    }
   
    .widgetImage {
        display: none;
    }
}

@media screen and (max-width: 850px) {
    .wrapper {
        width: 600px;
    }
    
    .callHeading {
        font-size: 22px;
    }
    
    .callStructure {
        flex-direction: column;
        align-items: flex-start;
    }
    
    .arrow {
        display: none;
    }
    
    .structureBlock {
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }

    .structureBlock:nth-child(n + 2) {
        margin-top: 20px;
    }
    
    .blockNum {
        width: 50px;
        height: 50px;
        font-size: 16px;
        line-height: 50px;
    }
    
    .blockText {
       font-size: 17px;
       margin-top: 0;
       margin-left: 20px;
    }
    
    .callWidget {
        height: 350px;
    }

    .textHeading {
        font-size: 23px;
    }
    
    .textP {
        font-size: 16px;
    }
    
    .formInputs {
        width: 200px;
    }
    
    .formBtn {
        width: 200px;
        height: 50px;
        font-size: 16px;
    }
}

@media screen and (max-width: 650px) {
    .wrapper {
        width: 400px;
    }
    
    .callHeading {
        font-size: 20px;
    }
    
    .widgetContent {
        padding: 0 0 0 30px;
    }

    .textHeading {
        font-size: 20px;
    }
    
    .textP {
        width: 300px;
        font-size: 14px;
    }
    
    .contentForm {
        margin-top: 20px;
    }
    
    .formInputs {
        width: 100%;
        flex-direction: column;
        margin-bottom: 25px;
        align-items: flex-start;
    }
    
    .formInput {
        width: 300px;
        font-size: 15px;
    }
    
    .formInput:nth-child(n + 2) {
        margin-left: 0;
        margin-top: 25px;
    }
    
    .formBtn {
        width: 170px;
        height: 40px;
        font-size: 14px;
    }
}

@media screen and (max-width: 425px) {
    .wrapper {
        width: 300px;
    }
    
    .callHeading {
        font-size: 16px;
    }
    
    .blockText {
        font-size: 14px;
    }
    
    .widgetContent {
        padding: 0 0 0 30px;
    }
    
    .textHeading {
        font-size: 16px;
    }
    
    .textP {
        width: 250px;
        font-size: 12px;
    }
    
    .formInput {
        width: 230px;
        font-size: 14px;
    }
    
    .formInput:nth-child(n + 2) {
        margin-left: 0;
        margin-top: 25px;
    }
    
    .formBtn {
        width: 170px;
        height: 40px;
        font-size: 14px;
    }
}

