.footer {
    width: 100%;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #28926C;
    color: #ffffff;
}

.wraper {
    width: 1440px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.footerLinks {
    width: 350px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    list-style: none;
}

.footerLink {
    text-decoration: none;
    color: #ffffff;
    font-weight: 600;
    font-size: 22px;
    cursor: pointer;
}

.footerLink:hover {
    text-decoration: underline;
}

.footerInfo {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
}

.footerPhone {
    font-weight: 700;
    font-size: 30px;
    margin-bottom: 10px;
}

.footerP {
    font-weight: 700;
    font-size: 16px;
    margin-bottom: 10px;
}

.footerSocials {
    display: flex;
    align-items: center;
    justify-content: center;
}

.footerSocial {
    width: 40px;
    height: 40px;
}

.footerSocial:nth-child(n + 2) {
    margin-left: 15px;
}

.footerSocial img {
    width: 40px;
    height: 40px;
}

@media screen and (max-width: 1500px) {
    .wraper {
        width: 1200px;
    }
    
    .footerLinks {
        width: 300px;
    }
    
    .footerLink {
        font-size: 20px;
    }
    
    .footerPhone {
        font-size: 25px;
    }
}

@media screen and (max-width: 1250px) {
    .wraper {
        width: 1000px;
    }
}

@media screen and (max-width: 1050px) {
    .wraper {
        width: 800px;
    }
    
    .footerLinks {
        width: 200px;
        flex-direction: column;
        align-items: flex-start;
    }

    .footerLink:nth-child(n + 2) {
        margin-top: 15px;
    }
}

@media screen and (max-width: 850px) {
    .wraper {
        width: 600px;
    }
    
    .footerPhone {
        font-size: 20px;
    }
    
    .footerP {
        font-size: 14px;
    }
}

@media screen and (max-width: 650px) {
    .wraper {
        width: 400px;
    }
    
    .footerLinks {
        width: 100px;
    }
    
    .footerLink {
        font-size: 16px;
    }

    .footerLink:nth-child(n + 2) {
        margin-top: 10px;
    }
    
    .footerPhone {
        font-size: 18px;
    }
    
    .footerP {
        font-size: 12px;
    }
    
    .footerSocial {
        width: 30px;
        height: 30px;
    }
    
    .footerSocial img {
        width: 30px;
        height: 30px;
    }
}

@media screen and (max-width: 425px) {
    .wraper {
        width: 300px;
    }
    
    .footerLinks {
        width: auto;
    }
    
    .footerLink {
        font-size: 16px;
    }

    .footerLink:nth-child(n + 2) {
        margin-top: 10px;
    }
    
    .footerPhone {
        font-size: 18px;
    }
    
    .footerP {
        font-size: 10px;
    }
    
    .footerSocial {
        width: 30px;
        height: 30px;
    }
    
    .footerSocial:nth-child(n + 2) {
        margin-left: 10px;
    }
    
    .footerSocial img {
        width: 30px;
        height: 30px;
    }
}
