.contact {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    padding-top: 70px;
    padding-bottom: 50px;
}

.wraper {
    width: 1440px;
}

.content { 
    display: flex;
    flex-direction: column;
}

.contactHeading {
    font-size: 30px;
    font-weight: 600;
    margin-top: 20px;
}

.contactInfo {
    display: flex;
    justify-content: space-between;
}

.contactBlock {
    margin-top: 30px;
    width: 450px;
}

.addressHeading {
    font-size: 20px;
    font-style: normal;
    margin-bottom: 10px;
}

.addressStreet {
    font-style: normal;
    font-size: 20px;
}

.addressStations {
    margin-top: 10px;
    font-style: normal;
    font-size: 20px;
}

.addressStation {
    color: #28926C;
    text-decoration: underline;
}

.phones {
    margin-top: 10px;
}

.phonesHeading {
    font-size: 18px;
    font-weight: 600;
}

.phonesContent {
    font-size: 20px;
    margin-top: 10px;
    color: #28926C;
}

.map {
    margin-top: 30px;
    height: 500px;
    border: none;
}

.miniNavLink {
    text-decoration: none;
    color: #28926C;
}

.miniNavLink:hover {
    text-decoration: underline;
}

.miniNavSpan {
    margin: 0 10px;
}

@media screen and (max-width: 1500px) {
    .wraper {
        width: 1200px;
    }
    
    .contactBlock {
        width: 400px;
    }
}

@media screen and (max-width: 1250px) {
    .wraper {
        width: 1000px;
    }
    
    .contactBlock {
        width: 300px;
    }
}

@media screen and (max-width: 1050px) {
    .wraper {
        width: 800px;
    }

    .contactHeading {
        font-size: 25px;
    }

    .addressHeading {
        font-size: 19px;
    }
    
    .addressStreet {
        font-size: 19px;
    }
    
    .addressStations {
        font-size: 19px;
    }
    
    .phonesHeading {
        font-size: 18px;
    }
    
    .phonesContent {
        font-size: 18px;
    }
    
    .contactBlock {
        width: 250px;
    }
}

@media screen and (max-width: 850px) {
    .wraper {
        width: 600px;
    }

    .contactInfo {
        flex-direction: column;
    }
    
    .contactBlock {
        width: 600px;
    }
}

@media screen and (max-width: 650px) {
    .wraper {
        width: 400px;
    }

    .contactHeading {
        font-size: 25px;
    }

    .addressHeading {
        font-size: 18px;
    }
    
    .addressStreet {
        font-size: 18px;
    }
    
    .addressStations {
        font-size: 18px;
    }
    
    .phonesHeading {
        font-size: 17px;
    }
    
    .phonesContent {
        font-size: 17px;
    }
    
    .contactBlock {
        width: 400px;
    }
}

@media screen and (max-width: 425px) {    
    .wraper {
        width: 300px;
    }

    .contactHeading {
        font-size: 23px;
    }

    .addressHeading {
        font-size: 16px;
    }
    
    .addressStreet {
        font-size: 16px;
    }
    
    .addressStations {
        font-size: 16px;
    }
    
    .phonesHeading {
        font-size: 15px;
    }
    
    .phonesContent {
        font-size: 15px;
    }
    
    .contactBlock {
        width: 300px;
    }
}