.about {
    width: 100%;
    height: auto;
    padding-top: 70px;
    padding-bottom: 50px;
    display: flex;
    justify-content: center;
}

.wraper {
    width: 1440px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.aboutHeading {
    font-size: 30px;
    font-weight: 600;
    margin-top: 20px;
}

.aboutContent {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 40px;
}

.contentBlock {
    width: 100%;
    height: auto;
    background-color: #F5F8F7;
    border-radius: 32px;
    padding: 30px;
    font-size: 17px;
    cursor: pointer;
}

.contentBlock:nth-child(n + 2) {
    margin-top: 40px;
}

.miniNavLink {
    text-decoration: none;
    color: #28926C;
}

.miniNavLink:hover {
    text-decoration: underline;
}

.miniNavSpan {
    margin: 0 10px;
}

@media screen and (max-width: 1500px) {
    .wraper {
        width: 1200px;
        
    }
}

@media screen and (max-width: 1250px) {
    .wraper {
        width: 1000px;
        
    }
}

@media screen and (max-width: 1050px) {
    .wraper {
        width: 800px;
        
    }
    
    .aboutHeading {
        font-size: 25px;
    }
}

@media screen and (max-width: 850px) {
    .wraper {
        width: 600px;
        
    }

    .contentBlock {
        font-size: 16px;
    }
}

@media screen and (max-width: 650px) {
    .wraper {
        width: 400px;
        
    }
}

@media screen and (max-width: 425px) {    
    .wraper {
        width: 300px;
        
    }

    .contentBlock {
        font-size: 15px;
    }
}